<script>
   /*************************************************************/
   /* Svelte component for changing SIMCA settings              */
   /*************************************************************/

   import { qchisq } from 'mdatools/distributions';
   import { Select, Container, Range, Button } from '../controls';

   // main properties of the whole app
   export let model = null;
   export let simca = null;
   export let className;


   let limType = 'classic';
   let ncomp = 1;
   let alpha = 5;
   let gamma = 1;

   const alphaOptions = [10, 5, 1, 0.1];
   const gammaOptions = [5, 1, 0.1, 0.01];

   // parameters that depend on limType
   let Nf, Nh, Nq, h0, q0;
   $: if (limType) {
      Nh = model.lim[limType].Nh;
      h0 = model.lim[limType].h0;
      Nq = model.lim[limType].Nq;
      q0 = model.lim[limType].q0;
      Nf = Nh.map((v, i) => v + Nq[i]);
   }

   // parameters that depends on Nf and alpha/gamma
   $: eCrit = Nf.map(v => qchisq(1 - alpha / 100, v));
   $: oCrit = Nf.map(v => qchisq(Math.pow(1 - gamma / 100, 1 / model.nrows), v));

   $: simca = {
      ncomp,
      alpha,
      limType,
      h0,
      q0,
      Nh,
      Nq,
      Nf,
      eCrit,
      oCrit,
      className
   };

   // function for downloading of PV-set
   function downloadModel(pcamodel, simca) {

      const blob = new Blob([JSON.stringify({
         pca: {
            class: pcamodel.class,
            ncomp: pcamodel.ncomp,
            nrows: pcamodel.nrows,
            ncols: pcamodel.ncols,
            center: pcamodel.center,
            scale: pcamodel.scale,
            v: Array.from(pcamodel.v)
         },
         simca: {
            h0: Array.from(simca.h0),
            Nh: Array.from(simca.Nh),
            q0: Array.from(simca.q0),
            Nq: Array.from(simca.Nq),
            Nf: Array.from(simca.Nf),
            eCrit: Array.from(simca.eCrit),
            oCrit: Array.from(simca.oCrit),
            ncomp : simca.ncomp,
            alpha: simca.alpha,
            gamma: simca.gamma,
            limType: simca.limType,
            className: simca.className
         }
      })], { type: 'application/json' });

      const url = window.URL.createObjectURL(blob)
      const a = document.createElement('a')
      a.setAttribute('href', url)
      a.setAttribute('download', 'model.json');
      a.click()
   }

</script>

<Container label="">
   <p class="title">Target class: {simca.className}</p>
</Container>

<Container label="# of PCs">
   <Range id="ncomp" bind:value={ncomp} min={1} max={model.ncomp} step={1} decNum={0} />
</Container>

<Container label='Alpha, %:'>
   <Select id='alpha' bind:value={alpha} options={alphaOptions} />
</Container>

<Container label='Gamma, %:'>
   <Select id='gamma' bind:value={gamma} options={gammaOptions} />
</Container>

<Container label='Limits:'>
   <Select id='limType' bind:value={limType} options={['classic', 'robust']} />
</Container>

<Container label=' '>
   <Button on:click={() => downloadModel(model, simca)}>Download model</Button>
</Container>
