import { Matrix } from 'mdatools/arrays';
import { pcares_get } from './pca';

/**
 * Compute classification statistics.
 *
 * @param {Array} pred - array with predicted categories ('regular', 'extreme', 'outlier').
 * @param {Array} ref - array with reference class names for each object.
 * @param {string} className - name of the target class.
 *
 * @returns {Object} JSON with classification statistics.
 */
function get_stat(pred, ref, className) {
   if (!ref || !className || ref.length !== pred.length) return null;
   let TP = 0, FP = 0, TN = 0, FN = 0;
   for (let i = 0; i < pred.length; i++) {
      if (pred[i] === 'regular' && ref[i] === className) TP = TP + 1;
      if (pred[i] === 'regular' && ref[i] !== className) FP = FP + 1;
      if (pred[i] !== 'regular' && ref[i] === className) FN = FN + 1;
      if (pred[i] !== 'regular' && ref[i] !== className) FP = FP + 1;
   }

   return {
      TP, FP, TN, FN,
      'accuracy': (TP + TN) / (TP + TN + FP + FN),
      'specificity': TN / (TN + FP),
      'sensitivity': TP / (TP + FN),
   }
}

/**
 * Compute SIMCA predictions for a particular component
 */
export function simca_predict_single(simca, pcares, distName, a, classes) {


   // get distances
   const f = pcares_get(pcares, distName, a + 1);
   const h = pcares_get(pcares, 'hdist', a + 1);
   const q = pcares_get(pcares, 'qdist', a + 1);

   const nrows = f.length;

   // get distance parameters
   const h0 = simca.h0[a];
   const Nh = simca.Nh[a];
   const q0 = simca.q0[a];
   const Nq = simca.Nq[a];
   const Nf = simca.Nf[a];

   // get critical limits
   const oCrit = simca.oCrit[a];
   const eCrit = simca.eCrit[a];

   // prepare array for categories
   const c = new Array(nrows);
   const cnum = {'regular': 0, 'extreme': 0, 'outliers': 0};

   for (let i = 0; i < nrows; i++) {
      const fi = f[i];
      if (fi < eCrit) {
         c[i] = 'regular';
         cnum.regular += 1;
      } else if (fi > oCrit) {
         c[i] = 'outlier';
         cnum.outliers += 1;
      } else {
         c[i] = 'extreme';
         cnum.extreme += 1;
      }
   }

   const stat =  classes && classes.length === nrows ? get_stat(c, classes, simca.className) : null
   return {h, h0, Nh, q, q0, Nq, f, Nf, eCrit, oCrit, c, cnum, stat};
}

/**
 * Compute SIMCA predictions.
 *
 * @param {Object} pcares - JSON with predicted PCA results.
 * @param {Object} set - JSON with dataset.
 * @param {Object} simca - JSON with SIMCA parameters.
 *
 * @returns {Object} JSON with SIMCA parameters.
 */
export function simca_predict(pcares, set, simca) {

   // get matrices with h and q distances
   const nrows = pcares.nrows;
   const ncomp = pcares.ncomp;

   // set f-distance name
   const distName = simca.limType === 'classic' ? 'fdistc' : 'fdistr';

   const res = new Array(ncomp);
   for (let a = 0; a < ncomp; a++) {
      res[a] = simca_predict_single(simca, pcares, distName, a, set.classes);
   }

   return {
      class: ['simcares'],
      v: res,
      className: simca.className,
      alpha: simca.alpha,
      limType: simca.limType,
      ncomp: simca.ncomp,
      nrows: nrows,
      classes: set.classes,
      labels: set.labels
   }
}


/**
 * Parse string with data from a CSV file and create a matrix with values.
 *
 * @param {string} strData - string with all data from the CSV file.
 * @param {string} sep - symbols used for separation of values (by default ",").
 * @param {boolean} hasHeader - logical, if first row must be treated as header or not.
 * @param {boolean} hasLabels - logical, if first column must be treated as object labels or not.
 * @param {boolean} hasClasses - logical, if column next to labels must be treated as class names or not.
 *
 * @returns {Object} JSON containing matrix with parsed values, header, values and decimal separators.
 */
export function parseCSV(strData, sep, hasHeader, hasLabels, hasClassNames) {


   // function which returns true if at least one value in array can not be parsed to float
   function hasNaN(vec) {
      return vec.some(v => Number.isNaN(Number.parseFloat(v.trim())));
   }

   // set default separator symbol
   if (sep === undefined) {
      sep = ",";
   }

   // setup decimal separator
   const dec = sep === ";" ? "," : ".";

   // remove BOM symbol if any
   strData = strData.replace(/^\uFEFF/gm, "").replace(/^\u00EF?\u00BB\u00BF/gm,"");

   // remove \r symbols
   strData = strData.replace(/\r/gm, "");

   // split string with data values into array of rows
   let rows = strData.split(/\n/);

   // check if the first row contains header
   let header = [];
   let startRow = 0;
   const firstRow = rows[0].split(sep);
   if (hasHeader || (hasHeader === undefined && hasNaN(firstRow))) {
      header = firstRow;
      startRow = 1;
   }

   // filter empty rows out
   rows = rows.filter(v => v.trim().length > 1);

   // check if first column contains labels
   let labels = [];
   let startColumn = 0;
   if (hasLabels) {
      startColumn = 1;
   }

   // check if next column contains class names
   let classNames = [];
   let classes = [];
   if (hasClassNames) {
      startColumn = startColumn + 1;
   }

   // number of rows and columns in future matrix
   const nrows = rows.length - startRow;
   const ncols = firstRow.length - startColumn;


   if (nrows < 1) {
      throw Error("Data file should contain at least 1 row with values.");
   }

   if (ncols < 1) {
      throw Error("Data file should contain at least 1 column with values.");
   }

   // set rows of data as columns of matrix - it is faster
   const data = Matrix.zeros(ncols, nrows);

   // find symbol used for decimals separator
   for (let r = startRow; r < nrows + startRow; r++) {

      // parsing: trim spaces and replace non-numbers with dot (.)
      let rowValuesAll = rows[r].split(sep)

      if (hasLabels) {
         labels.push(rowValuesAll[0]);
         rowValuesAll = rowValuesAll.slice(1);
      }

      if (hasClassNames) {
         const className = rowValuesAll[0];
         classes.push(className);
         if (classNames.length === 0 || classNames.indexOf(className) < 0) {
            classNames.push(className);
         }
         rowValuesAll = rowValuesAll.slice(1);
      }

      const rowValues = rowValuesAll.map(v => Number.parseFloat(v.trim().replace(dec, ".")));

      if (rowValues.some(v => Number.isNaN(v))) {
         throw Error("Some of the values can not be converted to numbers, check your CSV file and try again.");
      }

      if (rowValues.length != ncols) {
         throw Error("Wrong number of values in row #" + r);
      }

      data.v.set(new Float64Array(rowValues), (r - startRow) * ncols);
   }

   return {values: data.t(), header: header, labels: labels, classes: classes, classNames: classNames, sep:sep, dec:dec};
}