<script>
   /*************************************************************/
   /* Svelte component for showing app plots                    */
   /*************************************************************/

   // import plot and plot controls components
   import Container from '../controls/Container.svelte';
   import Button from '../controls/Button.svelte';
import { AcceptancePlot, AcceptancePlotControls } from '../plots';
   import { ExtremePlot, ExtremePlotControls } from '../plots';
   import { DFPlot, DFPlotControls } from '../plots';
   import { SensitivityPlot, SensitivityPlotControls } from '../plots';


   // Input parameters
   export let simcares;
   export let simca;

   function downloadPNG () {
      const scaleFactor = 3;
      const svgElement = plot_container.getElementsByTagName('svg')[0];


      // Create a new canvas element
      const canvas = document.createElement('canvas');
      const context = canvas.getContext('2d');

      // Set the canvas size to match the SVG element
      canvas.width = svgElement.clientWidth * scaleFactor;
      canvas.height = svgElement.clientHeight * scaleFactor;
      svgElement.setAttribute("width", canvas.width);
      svgElement.setAttribute("height", canvas.height);

      // Draw a white background
      context.fillStyle = 'white';
      context.scale(scaleFactor, scaleFactor)
      context.fillRect(0, 0, canvas.width, canvas.height);

      // Draw the SVG onto the canvas
      const svgXml = new XMLSerializer().serializeToString(svgElement);
      const img = new Image();
      img.src = 'data:image/svg+xml;base64,' +btoa(unescape(encodeURIComponent(svgXml)));

      img.onload = function () {

         context.drawImage(img, 0, 0, canvas.width, canvas.height);

         // Create a data URL for the canvas
         const dataUrl = canvas.toDataURL('image/png');

         // Create a temporary link and trigger the download
         const downloadLink = document.createElement('a');
         downloadLink.href = dataUrl;
         downloadLink.download = 'plot.png';
         downloadLink.click();
      };

   }

   // function for downloading of PV-set
   function downloadSVG() {
      const svg = plot_container.getElementsByTagName('svg')[0];
      svg.setAttribute('xmlns', 'http://www.w3.org/2000/svg');
      const blob = new Blob([svg.parentNode.innerHTML], { type: 'image/svg+xml' });

      const url = window.URL.createObjectURL(blob)
      const a = document.createElement('a')
      a.setAttribute('href', url)
      a.setAttribute('download', 'plot.svg');
      a.click()
   }

   // selected plot number and its parameters
   let selected_plot = 0;
   let params = null;
   let plot_container;

   // array with list of plots and their components
   const options = [
      {label: 'Acceptance', plotComponent: AcceptancePlot, controlsComponent: AcceptancePlotControls},
      {label: 'DoF', plotComponent: DFPlot, controlsComponent: DFPlotControls},
      {label: 'Extremes', plotComponent: ExtremePlot, controlsComponent: ExtremePlotControls},
      {label: 'Sensitivity', plotComponent: SensitivityPlot, controlsComponent: SensitivityPlotControls},
   ]

   // function to switch between plots
   function switchPlot(i) {
      params = null;
      selected_plot = i;
   }
</script>

<div class="plot-container">

   <!-- column with current plot -->
   <div bind:this={plot_container} class="plot-area">
      <svelte:component this={options[selected_plot].plotComponent} {simcares} {simca} {params} />
   </div>

   <!-- column with plot selectors and current plot settings -->
   <div class="plot-controls-area">
      {#each options as {label, plotComponent, controlsComponent}, i}
         {#if selected_plot === i}
         <svelte:component this={controlsComponent} bind:params={params} {simcares} />
         <Container label="Download as">
            <div class="buttons">
            <Button on:click={downloadSVG}>SVG</Button>
            <Button on:click={downloadPNG}>PNG</Button>
            </div>
         </Container>
         {:else}
         <button class="plot-selector" on:keydown={() => switchPlot(i)} on:click={() => switchPlot(i)}>{label}</button>
         {/if}
      {/each}
   </div>

</div>


<style>
   .buttons {
      position: relative;
      box-sizing: border-box;
      display: inline-block;
      flex: 1 1 auto;
      margin: 0;
      padding: 2px 0;
      width: auto;
      border-radius: 2px;
      cursor: default;

   }

   .plot-container {
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      padding: 0;
      display: grid;
      grid-template-rows: 1fr;
      grid-template-columns: 1fr minmax(270px, 30%);
   }

   .plot-area {
      padding: 1em;
      margin-top: 0.25rem;
      border-radius: 0.25em;
      background: #fefefe;
   }

   .plot-controls-area {
      padding: 0 0.25em 0 0.5em;
   }

   .plot-selector {
      cursor: pointer;
      color: #606060;
      background: #fffef480;
      box-sizing: border-box;
      padding: 0.5em 1em;
      margin: 0.25em 0;
      border-radius: 0.25rem;
      box-shadow: none;
      border: none;
      width: 100%;
      text-align: left;
      font-size: 1rem;
   }

   .plot-selector:hover {
      background: #e0e0e0;
      color: #403020;
   }

   @media(min-width:1100px) {
      .plot-container {
         grid-template-columns: 1fr minmax(285px, 30%);
      }
   }

   @media(min-width:1300px) {
      .plot-container {
         grid-template-columns: 1fr minmax(300px, 30%);
      }
   }

</style>